
.current-docu{
    position: relative;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.go-up-from-details{
    position: absolute;
    top: 0;
    width: 100vw;
    height: 10vh;
    cursor: url('../assets/cursor-up.png'),auto
}

.details {
    width: 80vw;
    height: 80vh;
    max-width: 1200px;
    border-radius: 1rem;

    background-image: linear-gradient(to top right, #c41bd3ca, #cd1b869e);
}

.header{
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 60%;
}

.infos{
    display: flex;
    flex-direction: column;
    width: 100%;
}

.image {
    width: 40%;
    max-width: 300px;
    opacity: 1;
    border-radius: 1rem;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.title{
    padding-top: 2%;
    padding-left: 2%;
}

.theme{
    padding-left: 2%;
    
}

.platforme{
    padding-left: 2%;
}

.note{
    padding-left: 2%;
}

.commentaire{
    font-size: 0.7rem;
    padding-top: 1%;
    padding-left: 2%;
    padding-right: 2rem;
}

.date{
    position: absolute;
    right: 6%;
    top: 1.8rem;
}

.resume{
    font-size: 0.7rem;
    padding-top: 1%;
    padding-left: 1rem;
    padding-right: 1rem;
    text-align: justify;
    text-justify: auto;
}